.doc-footer {
    background: $white;
    border-top: 1px solid #eee;
    font-size: 12px;
    .logo-footer {
        width: 80px;
        margin-bottom: 10px;
    }
    ul {
        padding: 0;
        margin: 0;
        li {
            display: inline-block;
            font-size: 12px;
            margin-right: 30px;
        }
    }

    &.style2 {
        .copyright {
            font-size: 12px;
            text-align: right;
            @media(max-width: 767px) {
                text-align: center;
            }
        }
        a {
        }
        &.short {
            padding-top: 20px;
        }
        padding-bottom: 30px;
        padding-top: 30px;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {

        }
        ul,
        .text-widget {
            line-height: 30px;
        }
        .social-links {
            text-align: left;
            ul {
                @media(max-width: 767px) {
                    text-align: center;
                }
            }
            margin:0;
            a {
                color: $white;
            }
        }
    }
    @media(max-width:767px) {
        text-align: center;
    }
}