.short-cart {
    position: relative;
    padding: 30px;
    ul {
        margin: 0;
        padding: 0;
        li {
            &:first-child {
                padding-top: 1px;
                a.remove-apartment {
                    margin-top: -25px;
                }
            }
            border-bottom:1px solid #f5f5f5;
            position:relative;
            display:block;
            padding:4px 20px 20px 80px;
            figure {
                position: absolute;
                left: 0;
                top: 15px;
                width: 60px;
            }
            a.apartment-title {
                font-size: 14px;
                font-weight: 600;
                color: $tc-sec;
                display: block;
                margin-top: 7px;
                margin-bottom: 2px;
            }
            span {
                font-weight: 700;
                font-size: 12px;
            }
            a.remove-apartment {
                width: 30px;
                height: 30px;
                line-height: 30px;
                position: absolute;
                right: 0;
                text-align: right;
                top: 50%;
                margin-top: -15px;
                color: $frColor;
            }
        }
    }
    table {
        width: 100%;
        td {
            padding: 15px 0;
            font-size: 14px;
            font-weight: 600;
            &:last-child {
                text-align: right;
            }
            strong {
                display: block;
                font-weight: 700;
            }
        }
    }
    a.btn {
        text-align: center;
    }
    .btn.remove-apartment {
        background: #cc3333 none repeat scroll 0 0;
        color: #fff;
        padding: 0;
        position: absolute;
        right: 0;
        top: 40%;
        width: 20px;
    }
    .date {
        font-weight: 100 !important;
    }
}