.info-cards {
  padding-bottom:30px;

  .info-card {

	  .card-content {
		  padding:0 30px;
		  background: #fff;
		  @media(max-width:767px){
			  padding:0 20px;
		  }
	  }




	background:$white;

	font-size:13px;
	& + .info-card {
	  margin-top:10px;
	}
	footer {
	  border-top:1px solid $c17;
		background: #f8f8f8;
		padding:10px 30px;
	  position:relative;
		textarea {
			border: 1px solid #eee;
			border-radius: 2px;
			height: 100px !important;
			margin: 10px 0;
		}
	  ul {
		margin:0;
		font-size:13px;
		@media(max-width:767px){
		  margin:0 0 15px;
		}
	  }
	}
	ul {
	  font-size:14px;
	  margin-bottom:15px;
	}
	.card-inner {
	  position:relative;
	  padding:25px 140px 25px 100px;
	  @media(max-width:767px){
		padding-left:0;
		padding-right:110px;
	  }
	  .card-actions {
		@media(max-width:767px){
		  right:0px;
		}
		right:40px;
		top:25px;
		width:100px;
		position:absolute;
		a.btn {
		  width:100%;
		  display:block;
		  text-align:center;
		  & + a.btn {
			margin-top:10px;
		  }
		}
	  }
		.card-icon {
			img {
				border-radius: 2px;
				box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14);
			}
		}
	  .card-icon {
		width:84px;
		height:84px;
		line-height:84px;
		border-radius:50%;
		font-size:50px;
		text-align:center;
		position:absolute;
		top:28px;
		left:0;
		@media(max-width:767px){
		  position:relative;
		  top:auto;
		  left:auto;
		  margin-bottom:15px;
		}
	  }
	  .card-track {
		margin:0;
		padding:0;
		font-size:14px;
		font-weight:600;
		color:$tc-sec;
		margin-bottom:15px;
		li {
		  @media(max-width:767px){
			display:block;
		  }
		  display:inline-block;
		  & + li:before {
			content:"\f101";
			font-family:"FontAwesome";
			width:20px;
			text-align:center;
			display:inline-block;
		  }
		  a {
			color:inherit;
		  }
		}
	  }
	}
  }
}


