.btn {
    position: relative;
    display: inline-block;
    padding: 10px 20px;
    text-decoration: none !important;
    font-size: $fs-small;
    font-weight: 400;
    border: none;
    text-transform: capitalize;
    border-radius: 2px;
    &.block-element {
        text-align: center;
    }
}

.buttons-demo div.demo-div {
    margin-bottom: 20px;
    .btn {
        &.btn-transparent,
        &.btn-transparent-2 {
            margin: -2px;
        }
    }
}

.btn-success {
    background-color: $success;
}

.btn-primary {
    background: #6286A9
}

.btn-blue {
    background: #6286A9;
    color: $white;
    &:hover {
        background: $secColor;
        color: $white;
    }
}

.btn-small {
    padding: 12px 30px;
}

.btn-medium {
    padding: 12px 45px;
}

.btn-large {
    padding: 20px 70px;
}

.btn-xl {
    padding: 24px 85px;
}

.btn-fw-normal {
    font-weight: 400 !important;
}

.btn.danger-hover:hover {
    background: $c12;
    color: $white;
}

.btn-grey {
    color: $tc-default;
    background: $fvColor;
}

.btn-blue-grey {
    color: $white;
    background: #DDA0DD;
}

.btn-dark-grey {
    color: $tc-default;
    background: #d2d2d2;
}

.btn-md {
    padding: 15px 40px;
}

.btn-white {
    background: $white;
    color: $c16;
    &:hover {
        background: $priColor;
        color: $white;
    }
}

.btn-black {
    background: #151515;
    color: #fff;
    &:hover {
        background: $priColor;
        color: $white;
    }
}

.btn-yellow {
    background: $c3;
    color: $white;
    &:hover {
        background: $secColor;
        color: $white;
    }
}

.btn-green {
    background: $secColor;
    color: $white;
    &:hover {
        background: $priColor;
        color: $white;
    }
}

.btn-transparent {
    border: 2px solid $terColor;
    color: $c16;
    &:hover {
        background: $priColor;
        color: $white;
        border-color: $priColor;
    }
}

.btn-transparent-2 {
    border: 2px solid #151515;
    color: #151515;
    &:hover {
        background: $priColor;
        color: $white;
        border-color: $priColor;
    }
}

a.link {
    text-decoration: underline;
}

.danger-hover:hover {
    background: $c12 !important;
    color: $white !important;
}

.btn-primary-line {
    background: transparent;
    border: 1px solid #6286a9;
    color: #6286a9;
    &:hover {
        background: #6286a9;
        color: #fff;
    }
}

.btn-success-line {
    background: transparent;
    border: 1px solid #6286a9;
    color: #6286a9;
    &:hover {
        background: #6286a9;
        color: #fff;
    }
}

.btn-yellow {
    background: #FFAF02;
    &:hover {
        background: #FFAF02;
        opacity: 0.8;
    }
}