/*==================================
1-Preloader
===================================*/

.pageLoader{
  position: fixed;
  bottom: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: #fff;
  opacity: 0.5;
  z-index: 999;
  visibility: visible;
  opacity: 1;
  transition: opacity 2s linear;
}
.pageLoader.active{
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 2s, opacity 2s linear;
}


.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

