.search-widget{
  background:#f8f8f8;
  position:relative;
  height:$h40;
  margin-bottom:20px;
  input[type="text"]{
	background:#f8f8f8;
	border:none;
	width:100%;
	height:100%;
	padding-left:15px;
	padding-right:40px;
	max-width:none;
  }
  button[type="submit"]{

		  border: medium none;
		  color: #fff;
		  height: 80%;
		  position: absolute;
		  right: 7px;
		  top: 5px;
		  width: 108px;
	  background: #6286A9;
  }
  .uzr-panel &{
	max-width:220px;
  }

  &.style2{
	padding-right:170px;
	background:none;
	button[type="submit"]{
	  width:160px;
	  background:$secColor;
	  color:$white;
	  font-size:14px;
	  font-weight:700;
	}
  }
}