.uzr-dashboard {
    background: #fff none repeat scroll 0 0;
    margin-bottom: 0;
    .sticky {
        top: 65px;
        z-index: 99;
    }
}

.uzr-options-area {
    position: relative;
    .uzr-sidebar {
        float: left;
        width: 24%;
        background: $fvColor;
        @media (max-width: 992px) {
            float: none;
            width: 100%;
            display: none;
        }
    }
    .uzr-panels {
        @media (max-width: 992px) {
            float: none;
            width: 100%;
        }
        float:left;
        width:76%;
        background:$fvColor;
        .uzr-panel {
            &>.inner {
                padding: 60px;
                @media (max-width: 1199px) {
                    padding: 20px;
                }
            }
            &>header {
                @media (max-width: 992px) {
                    padding: 20px;
                }
                padding:60px;
                background:$white;
                h2,
                h3,
                h4,
                h5 {
                    margin: 0;
                }
                .search-widget {
                    margin-bottom: 0;
                }
            }
            .panel-actions {
                @media (max-width: 1020px) {
                    display: none;
                }
                ul.panel-action-list {
                    margin: 0;
                    padding: 0;
                    &.text-right {
                        float: right;
                    }
                    &>li {
                        display: block;
                        float: left;
                        margin-right: 4px;
                        position: relative;
                        a {
                            display: block;
                            padding: 8px 18px;
                            background: $white;
                            color: inherit;
                            font-size: 12px;
                            text-decoration: none;
                            &:hover {
                                color: $white;
                                background: $priColor;
                            }
                        }
                    }
                }
            }
            .selection-dropdown {
                padding: 5px 30px 5px 10px;
                background: $white;
                position: relative;
                button {
                    background: none;
                    width: 25px;
                    border: none;
                    position: absolute;
                    right: 0;
                    top: 0;
                    height: 100%;
                    i {
                        position: absolute;
                        top: 50%;
                        line-height: 0;
                        left: 0;
                        width: 100%;
                        text-align: center;
                        color: $frColor;
                    }
                }
                ul {
                    display: none;
                    border: 1px solid $terColor;
                    text-align: left;
                    position: absolute;
                    top: 100%;
                    right: 0;
                    width: 150px;
                    z-index: 200;
                    background: $white;
                    margin: 0;
                    padding: 0;
                    li {
                        display: block;
                        a {
                            display: block;
                            padding: 10px 2px;
                            text-decoration: none;
                        }
                        &.active a,
                        & a:hover {
                            background: $fvColor;
                        }
                    }
                }
                &.active ul {
                    display: block;
                }
            }
            .listing-actions {
                border: none;
                margin: 0 0 0 10px;
                padding: 0;
            }
        }
    }
    .uzr-quick-nav {
        position: relative;
        margin: 0;
        padding: 0;
        text-align: right;
        @media (max-width: 992px) {
            text-align: left;
            margin-top: 10px;
        }
        li {
            display: inline-block;
            margin-left: 4px;
            a {
                text-decoration: none;
                color: inherit;
                display: block;
                width: 60px;
                height: 55px;
                background: $fvColor;
                position: relative;
                i {
                    position: absolute;
                    top: 50%;
                    line-height: 0;
                    left: 0;
                    width: 100%;
                    text-align: center;
                    font-size: 20px;
                }
                span {
                    display: block;
                    font-size: 10px;
                    color: $white;
                    font-weight: 600;
                    top: 0;
                    right: 0;
                    position: absolute;
                    width: 16px;
                    height: 16px;
                    text-align: center;
                    line-height: 16px;
                }
            }
        }
    }
}

.apartment-admin-actions,
.msg-actions {
    @media(max-width: 767px) {
        .uzr-panel & {
            position: relative;
            top: auto;
            right: auto;
            ul {
                text-align: left;
                padding-bottom: 20px;
            }
        }
    }
    position:absolute;
    right:25px;
    top:50%;
    margin-top:-10px;
    ul {
        margin: 0;
        padding: 0;
        li {
            display: inline-block;
            a {
                display: block;
                color: inherit;
                text-decoration: none;
                font-size: 20px;
                &:hover {
                    color: $c7;
                }
            }
        }
    }
}

.msg-list {
    padding: 30px 0;
    position: relative;
    .msg-unit {
        &.msg-unread {
            background: $c18;
        }
        &.msg-selected {
            background: $c21;
        }
        font-size:14px;
        position:relative;
        padding-right:150px;
        background:$white;
        margin-bottom:3px;
        @media(max-width:767px) {
            padding-right: 15px;
        }
        .msg-actions {
            .custom-check {
                top: 1px;
            }
            @media(max-width:767px) {
                position: absolute;
                left: 135px;
                bottom: 0;
            }
        }
        .msg-content {
            position: relative;
            padding: 25px 25px 25px 160px;
            @media(max-width: 767px) {
                padding-left: 135px;
                padding-bottom: 50px;
            }
            header {
                display: block;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                color: $tc-sec;
            }
            .msg-body {
                @media(max-width: 767px) {
                    display: none;
                }
                p {
                    font-size: 13px;
                    font-weight: normal;
                    line-height: 24px;
                    margin: 8px 0;
                }
                .msg-time {
                    font-size: 13px;
                }
            }
            figure {
                img {
                    border-radius: 3px;
                }
                box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14);
                left: 33px;
                overflow: hidden;
                position: absolute;
                top: 17%;
            }
            a.msg-open {
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                display: block;
                text-indent: -999999999px;
                overflow: hidden;
                z-index: 20;
            }
        }
    }
}

._template_dashboard {
    .text-with-icons {
        img {
            height: 60px;
        }
        background: #fff;
        border-radius: 2px;
        border: 1px solid #eee;
        padding: 50px 30px;
    }
    .accordion {
        margin-bottom: 10px;
        header {
            a {
                font-size: 13px;
            }
        }
        .accordion-content {
            border-top: 1px solid #eee;
            line-height: inherit;
            padding: 0;
        }
    }
    .nt-tab-simple {
        background: #fff;
        border: 1px solid #eee;
        box-shadow: 0 1px 5px -2px rgba(0, 0, 0, 0.14);
        margin: 20px 0;
        ul li+li {
            border-top: none;
        }
        li {
            display: inline-block;
            a {
                font-size: 12px;
                padding: 20px 15px 20px 50px;
                span {
                    font-size: 14px;
                    width: 44px;
                }
            }
        }
    }
}

#coverSlim {
    max-height: 312px;
}