.heading {
	font-size: 16px;
	padding-bottom: 30px;
	i {
		color: #77c04b;
		font-size: 80px;
		margin: 50px;
	}
	p {
		font-size: 14px;
		letter-spacing: 1px;
		padding-top: 16px;
		word-spacing: 2px;
	}
	&.style-bg {
		background: $fvColor;
	}
	@media(max-width: 500px) {
		font-size: 18px;
	}
	&.big {
		padding: 40px 0;
		@media(max-width: 767px) {
			padding-top: 0;
		}
	}
	&.small {
		font-size: 14px;
		padding-bottom: 50px;
	}
	&.style2 {
		font-size: 14px;
		h2 {
			margin: 0;
			@media(min-width: 1200px) {
				font-size: 40px;
			}
		}
		p {
			margin-top: 0;
		}
	}
	h1, h2, h3 {
		text-transform: none;
	}
	h4, h5 {
		color: $tc-default;
	}
	.text-bold {
		h1, h2, h3, h4, h5 {
			font-weight: 700;
		}
	}
	h1, h2, h3, h4, h5 {
		margin-top: 0;
		margin-bottom: 0;
		strong {
			font-weight: 700;
			position: relative;
			color: #6286A9;
			z-index: 5;
			&:after {
				content: "";
				width: 100%;
				left: 0;
				bottom: 0;
				background: url('#{$appImages}/basic/line.png') no-repeat center center;
				height: 9px;
				position: absolute;
				z-index: -1;
			}
		}
	}
	&.long {
		text-align: center;
		padding: 180px 0;
		font-size: 14px;
		@media(max-width: 767px) {
			padding: 30px 0;
		}
	}
}

.icon-heading {
	padding: 5px 0;
	h1, h2, h3, h4 {
		padding-left: 50px;
		position: relative;
		i {
			position: absolute;
			top: 50%;
			line-height: 0;
			font-size: 40px;
			left: 0;
			color: $priColor;
		}
	}
}

section{
	.heading{
		h1, h2{
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 2px;
			text-transform: uppercase;
		}
	}
}