/*
 * Row with equal height columns
 * --------------------------------------------------
 */

.row-eq-height {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

strong {
    font-weight: 600;
}

.text-uppercase {
    text-transform: uppercase !important;
}

.text-right {
    text-align: right !important;
}

.text-left {
    text-align: left !important;
}

.text-center {
    text-align: center !important;
}

.text-bold {
    font-weight: 700 !important;
}

.text-semi-bold {
    font-weight: 600 !important;
}

.text-italic {
    font-style: italic !important;
}

a {
    text-decoration: none !important;
}

a.link {
    text-decoration: underline;
    &.link-pink {
        color: $c2;
    }
}

.web-app {
    overflow: hidden;
    height: 100%;
}

.page {
    margin-bottom: 100px;
}

@media(max-width:767px) {
    .hidden-xs {
        display: none;
    }
}

.white-bg {
    background: $white;
}

.blue-bg {
    background: $priColor;
}

.container {
    @media(max-width: 1199px) {
        width: 100% !important;
    }
}

.section {
    padding: 90px 0;
    &.style2 {
        padding-bottom: 90px;
    }
}

.wide-element {
    width: 100% !important;
    max-width: 100% !important;
}

.block-element {
    width: 100%;
    display: block !important;
}

.imgAsBg>img {
    max-height: 100%;
    max-width: 100%;
    width: auto;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

