.basic-card {
    background: $white;
    @media(max-width: 767px) {
        background: none;
        padding: 0;
    }
    padding:0 25px;
    &+.basic-card {
        margin-top: 10px;
    }
    &>header {
        padding: 25px 0;
        border-bottom: 1px solid $c17;
        @media(max-width: 767px) {
            padding: 10px 0;
        }
    }
    &>.inner {
        padding: 25px;
        @media(max-width: 767px) {
            padding: 10px 0;
        }
        form {
            padding: 25px 0;
            @media(max-width: 767px) {
                padding: 15px 0 0;
            }
        }
    }
}

