.contextual-page {
    padding: 0 60px;
    background: $white;
    @media(max-width: 1199px) {
        margin: 0 -15px;
        padding: 0 15px;
    }
    .slick-margin-fix {
        margin: 0 -60px 100px -60px;
        @media(max-width: 1199px) {
            margin: 0 -15px 15px -15px;
        }
    }
    .page-margin-fix {
        margin: 0 -60px;
    }
    .page-header {
        margin: 0 -60px;
        padding: 40px 60px;
        background: $fvColor;
        p {
            margin: 0;
        }
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin: 0;
            &+p {
                margin-top: 10px;
            }
        }
    }
}

