.avatar {
    position: relative;
    top: 1px;
    display: inline-block;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif !important;
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.avatar {
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 20px;
}

.avatar-sm {
    width: 16px;
    height: 16px;
    line-height: 16px;
    font-size: 10px;
}

.avatar-md {
    width: 64px;
    height: 64px;
    line-height: 64px;
    font-size: 40px;
}

.avatar-lg {
    width: 128px;
    height: 128px;
    line-height: 128px;
    font-size: 80px;
}

