.archive{


  header.archive-title{

    background-color: #77c04b;
    background-image: url("../assets/img/poly.png");

    padding: 60px;
    position: relative;
    text-align: center;
    text-transform: capitalize;


    h2{
      color: #fff;
      font-size: 50px;
      font-weight: 700;
      letter-spacing: 10px;
      text-transform: uppercase;
    }
    h4{
      color: #fff;
      font-size: 16px;
      font-weight: 100;
      padding-top: 20px;

    }
  }
  .apartments.row {
    margin-top: 10px;
  }
  //.container{
  //  width: 970px;
  //}
  h1{
    font-size: 20px;
  }
}




.child-cats{
  ul {
    margin: 0;
    padding: 8px 0;
    li {
      background: #f7f7f7 none repeat scroll 0 0;
      display: inline-block;
      margin: 1px;
      padding: 20px 10px;
      position: relative;
      width: 19.795%;
      @media (max-width: 1024px) {
        text-align: center;
        width: auto;
        padding: 20px;
      }
      @media (max-width: 768px) {
        text-align: center;
        width: auto;
        padding: 10px;
      }

      a{
        span {
          bottom: 0;
          height: 32%;
          left: 43px;
          margin: auto;
          position: absolute;
          right: 0;
          top: 0;
          vertical-align: middle;
          width: 125px;
          font-size: 12px;
          @media (max-width: 1024px) {
            display: none;
          }
        }

      }
      &:hover{

      }
      i {
        background: #fff;
        border: 1px solid #6286a9;
        border-radius: 50%;
        color: #4693d3;
        display: inline-block;
        font-size: 18px;
        height: 40px;
        line-height: 40px;
        margin-right: 12px;
        text-align: center;
        vertical-align: middle;
        width: 40px;
      }
    }
  }

}


.archive-search{
  header.archive-title{

    padding: 0 0 35px 0;
    position: relative;

    text-transform: capitalize;
    h2{
      font-size: 26px;
    }
    h4{
      font-size: 14px;
      padding-top: 20px;

    }


  }
  .apartments.row {
    margin-top: 10px;
  }
  //.container{
  //  width: 970px;
  //}
  h1{
    font-size: 20px;
  }

  .filterd{
    position: relative;
    .wrap {
      background: #fff none repeat scroll 0 0;
      box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14);
      padding: 12px;
      position: relative;
      text-align: left;
    }
    .filter-tab {
      margin: 0;
      position: absolute;
      right: 0;
      text-align: right;
      top: -34px;
      li {
        background: #FFAF02;
        display: inline-block;
        font-size: 12px;
        margin-bottom: 0;
        padding: 8px 26px;


        i{
          font-size: 18px;
          color: #fff;
        }
        &:first-child {
          background: rgba(0, 0, 0, 0);
          padding: 0 8px;
        }
      }
    }
    @media (max-width: 767px) {
      display: none;
    }
  }
}
