.infinite-container {
    overflow: visible;
    margin-bottom: 250px;
    padding: 10px;
}

.loading {
    bottom: 0;
    display: none;
    left: 0;
    margin-bottom: 113px;
    position: absolute;
    right: 0;
    text-align: center;
}

.activeWay {
    .loading {
        display: block;
    }
}

.call {
    background-clip: border-box;
    background-color: transparent;
    background-image: url("http://1.bp.blogspot.com/-DEqXYw53iFc/VEKYUdAOCsI/AAAAAAAAG6k/Zup8EkkSv6Q/s1600/cbm2.png");
    background-origin: padding-box;
    background-repeat: repeat-x;
    color: #fff;
    padding: 100px;
    h2 {
        color: #fff;
    }
    .quick-post {
        box-shadow: 0 0 14px 15px rgba(0, 0, 0, 0.14);
    }
}

.bt {
    border-bottom: 1px solid #eee;
}

.toggle-content {
    border-top: 1px solid #eee;
    display: none;
}

.quick-actions img {
    border-radius: 2px;
    height: 30px;
}

.no-content {
    .cat-box {
        width: 100% !important;
        .inner {
            background: transparent;
            margin: 200px 0;
        }
        .inner:hover {
            background: transparent;
            box-shadow: none !important;
        }
        // margin: 200px;
    }
}

.cover {
    min-height: 320px;
    background-size: cover !important;
}

.search-widget input[type="text"] {
    border-radius: 1px;
    //box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.14);
    background: #f8f8f8;
}

.mega-dropdown {
    border: none;
    height: 50px;
}

.search-filters {
    border-left: 1px solid #eee;
    padding: 5px 0;
}

.doc-header a.quick-post,
.btn-search {
    border-radius: 2px;
    font-weight: 400;
    margin-left: 10px;
    margin-top: 6px;
    padding: 10px 20px;
}

.uzr-dashboard {
    padding: 0;
}

.doc-header .container>.inner .app-logo {
    top: 45px;
}

.slim-error {
    font-size: 10px;
    padding: 0;
    text-align: left;
}

.cats {
    background: #fff none repeat scroll 0 0;
    border: 1px solid #eee;
    padding: 20px;
    overflow: hidden;
    ul {
        padding: 0;
        li {
            position: relative;
            display: block;
            float: left;
            width: 50%;
            padding: 10px 10px;
            color: #6286A9;
            a {
                font-size: 12px;
                color: #6286A9;
            }
            &::after {
                content: "";
                font-family: "FontAwesome";
                line-height: 0;
                position: absolute;
                left: 0;
                top: 50%;
            }
        }
    }
}

.btn-city {
    background: transparent none repeat scroll 0 0;
    border: medium none;
    left: 130px;
    position: absolute;
    top: 18px;
    z-index: 999;
}

.btn-category {
    background: transparent none repeat scroll 0 0;
    border: medium none;
    left: 316px;
    position: absolute;
    top: 18px;
    z-index: 999;
}

aside.filter {
    header {
        padding: 0 10px;
    }
    select {
        border: 1px solid #e0e0e0;
        height: 40px;
        padding: 0 10px;
        width: 100%;
    }
}

.label {
    color: #686868;
}

#map_canvas {
    width: 100%;
    height: 400px;
}

.doc-footer img {
    padding-top: 10px;
}

.autocomplete-suggestions {
    border: 1px solid #999;
    background: #FFF;
    overflow: auto;
}

.autocomplete-suggestion {
    padding: 2px 5px;
    white-space: nowrap;
    overflow: hidden;
}

.autocomplete-selected {
    background: #F0F0F0;
}

.autocomplete-suggestions strong {
    font-weight: normal;
    color: #3399FF;
}

.autocomplete-group {
    padding: 2px 5px;
}

.autocomplete-group strong {
    display: block;
    border-bottom: 1px solid #000;
}

.filter-options-widget {
    .iradio_square-aero {
        margin-right: 18px;
    }
}

.booking {
    padding: 30px;
    background: #fafafa;
    margin: 2px;
    margin-top: 0;
    label {
        font-size: 12px;
        font-weight: 400;
    }
    .form-control.datetimepicker {
        border-radius: 0;
        &::after {
            background: turquoise none repeat scroll 0 0;
            content: "";
            display: block;
            font-family: fontawesome;
            height: 20px;
            left: 0;
            position: absolute;
            width: 20px;
        }
    }
    .btn {
        box-shadow: 0 4px 2px 1px rgba(0, 0, 0, 0.1);
        color: #fff;
        font-size: 14px !important;
        padding: 15px;
        width: 100%;
        &::after {
            content: "";
            font-family: fontawesome;
            opacity: 0.6;
            position: absolute;
            right: 14px;
        }
    }
}

.featured-img {
    background: transparent url("/assets/img/placeholder.jpg") no-repeat scroll center top / cover;
    background-size: cover;
    min-height: 134px;
    @media(max-width: 1024px) {
        min-height: 175px;
    }
    @media(max-width:768px) {
        min-height: 185px;
    }
}

* {
    box-sizing: border-box;
}

#progress {
    padding: 0;
    list-style-type: none;
    font-family: arial;
    font-size: 12px;
    clear: both;
    line-height: 1em;
    margin: 0 -1px;
    text-align: center;
}

#progress li {
    float: left;
    padding: 18px 30px 18px 40px;
    background: #333;
    color: #fff;
    position: relative;
    border-top: 1px solid #666;
    border-bottom: 1px solid #666;
    width: 32%;
    margin: 0 1px;
}

#progress li:before {
    content: '';
    border-left: 24px solid #fff;
    border-top: 24px solid transparent;
    border-bottom: 24px solid transparent;
    position: absolute;
    top: 0;
    left: 0;
}

#progress li:after {
    content: '';
    border-left: 24px solid #333;
    border-top: 24px solid transparent;
    border-bottom: 24px solid transparent;
    position: absolute;
    top: 0;
    left: 100%;
    z-index: 20;
}

#progress li.active {
    background: #555;
}

#progress li.active:after {
    border-left-color: #555;
}

.form-wrapper {
    .btn-danger {
        bottom: 54px;
        position: absolute;
        right: 40px;
    }
}