.alert {
    position: relative;
    line-height: normal;
    font-size: 12px;
    font-weight: 400;
    padding: 14px 20px;
    border-radius: 0;
    >.dismiss-alert {
        font-size: 10px;
        position: absolute;
        top: 0;
        right: 0;
        width: 25px;
        height: 25px;
        border: none;
    }
      h4 {
        font-size: 18px;
    }
}

.alert-info {
    background-color: #fff;
    border-color: #bce8f1;
    border-radius: 3px !important;
    color: #31708f;
  
}
.alert-success {
    background-color: #fff;

}