.apartment-single {
  position: relative;
  margin-bottom: 100px;
  article {
    .inner {
      font-size: 12px;
      letter-spacing: 0.5px;
      padding: 20px 40px 40px;
      h3 {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 2px;
        padding-bottom: 20px;
        padding-top: 30px;
        text-transform: uppercase;
        &::after {
          background: #eee;
          content: "";
          display: block;
          height: 3px;
          margin-top: 10px;
          width: 72px;
        }
      }
      p {
        font-size: 12px;
        letter-spacing: 0.5px;
      }
    }
  }
  aside {
    padding: 0;
    @media(min-width: 360px) {
      margin-top: 54px;
    }
    .widget {
      box-shadow: 0 -1px 10px 0 rgba(0, 0, 0, 0.14);
      margin-bottom: 20px;
    }
    .estimatedPrice {
      border: 2px solid #77c04b;
      border-radius: 2px;
      margin-bottom: 20px;
      margin-top: 20px;
      padding: 20px;
    }
    .availability {
      color: #77c04b;
      font-size: 16px;
      left: 36px;
      position: absolute;
      top: 40px;
    }
    .avatar {
      border-radius: 50%;
      font-size: 28px;
      height: 55px;
      line-height: 55px;
      width: 55px;
    }
  }
  .apartment-content {
    header {
      background: #fff none repeat scroll 0 0;
      margin-top: 20px;
      padding: 15px 30px 30px;
      border-bottom: 1px solid #eee;
      h1 {
        font-size: 18px;
        font-weight: 600;
        line-height: 40px;
        text-transform: capitalize;
      }
    }
    margin-bottom: 10px;
    & > .inner {
      background: $white;
      & > header {
        @media(max-width: 992px) {
          padding: 60px 20px 20px;
        }
        padding: 35px 150px 35px 40px;
        position: relative;
        z-index: 99;
        -webkit-box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
        -moz-box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
        box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
        .info-icons {
          position: absolute;
          top: 50%;
          right: 40px;
          margin-top: -15px;
          @media(max-width: 992px) {
            top: 20px;
            right: auto;
            left: 20px;
            margin-top: 0;
          }
        }
        h1 {
          font-size: $h3;
          margin: 0 0 15px;
        }
      }
      .quick-info {
        padding: 45px 40px 35px;
        font-size: 14px;
        @media(max-width: 992px) {
          padding: 20px 20px 25px;
        }
        ul {
          margin: -5px;
          padding: 0;
          li {
            @media(max-width: 992px) {
              float: none;
              width: 100%;
            }
            float: left;
            width: 50%;
            display: block;
            padding: 5px;
            & > .inner {
              background: $fvColor;
              padding: 10px 15px;
              & > span {
                float: left;
                width: 50%;
                text-align: right;
              }
              span.label {
                color: $tc-sec;
                text-align: left;
              }
            }
          }
        }
      }
      .text-widget {
        @media(max-width: 992px) {
          padding: 0 20px;
        }
        line-height: 28px;
        padding: 0 40px 40px;
        header {
          padding-bottom: 30px;
          h2,
          h3,
          h4 {
            margin: 0;
          }
        }
        .inner {
          padding: 0;
          & > p {
            margin-top: 0;
            margin-bottom: 30px;
          }
          & > p:last-child {
            margin-bottom: 0;
          }
        }
      }
      & > footer {
        background: $fvColor;
        font-size: $h6;
        padding: 40px;
        .fa {
          color: $priColor;
        }
        a {
          font-weight: 600;
          text-decoration: underline;
        }
      }
    }
  }
  .dropdown {
    margin-top: 20px;
    @media(min-width: 360px) {
      margin-top: 10px;
    }
  }
  .dropdown-toggle {
    @media (min-width: 360px) {
      display: none;
    }
  }
  .tabs {
    background: $bg-default;
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 100%;
    @media (max-width: 360px) {
      display: none;
    }
    li {
      border-radius: 2px;
      display: inline;
      margin-right: 5px;
      padding: 5px 27px 7px;
      background: #edeff0;
      a {
        color: #888;
        font-size: 12px;
        text-decoration: none;
      }
      li:hover {
        opacity: 0.6;
      }
      .label.label-info {
        color: #fff;
        font-weight: 400;
        margin-left: 10px;
      }
    }
    .active {
      background: #77c04b;
      a {
        color: #fff;
      }
    }
  }
}

.renter-avatar {
  img {
    border-radius: 2px;
  }
  margin-right: 15px;
  width: 50px;
}

.renter-info > h4 {
  font-size: 14px;
  font-weight: 600;
  margin: 0 0 10px;
}

.booking {
  .alert {
    border-radius: 1px;
    font-size: 12px;
    font-weight: 400;
    line-height: normal;
    padding: 10px;
  }
  .label {
    color: #fff !important;
    font-weight: 400;
    padding: 4px 7px;
  }
  .list {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    li {
      border-bottom: 1px solid #eee;
      font-size: 13px;
      list-style: outside none none;
      padding: 7px 0;
      a {
        font-size: 13px;
        text-decoration: none;
        color: #3164B2;
        i {
          margin-right: 14px;
          font-size: 14px;
        }
      }
    }
  }
}

.rating-container {
  position: relative;
  .counter {
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    // display: none;
    font-size: 15px;
    height: 20px;
    line-height: 0;
    margin-top: -10px;
    min-width: 20px;
    padding: 5px;
    position: absolute;
    left: 175px;
    top: 50%;
  }
}

div.rating-container div.counter::before,
div.rating-container div.counter *::before {
  border-color: transparent rgba(0, 0, 0, 0.8) transparent transparent;
  border-style: solid;
  border-width: 5px 5px 5px 0;
  content: "";
  display: block;
  height: 0;
  left: -10px;
  margin-top: -5px;
  position: relative;
  top: 50%;
  width: 0;
}

.status {
  color: #fff;
  font-size: 12px;
  margin-left: 10px;
  padding: 2px 11px;
  text-transform: capitalize;
  position: absolute;
  right: 60px;
}

.new .status,
.Pending .status {
  background: #5ea1d9;
}

.accepted .status {
  background: #FBD988;
}

.rejected .status {
  background: #f95858;
}

.completed .status {
  background: #77C04B;
}

.cancelled .status {
  background: #768798;
}

.bookings-list {
  margin-top: 10px;
  .info-card {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14);
    &.new,
    &.Pending {
      border-top: 3px solid #5EA1D9;
    }
    &.accepted {
      border-top: 3px solid #FBD988;
    }
    &.rejected {
      border-top: 3px solid #f95858;
    }
    &.completed {
      border-top: 3px solid #77C04B;
    }
    &.cancelled {
      border-top: 3px solid #768798;
    }
  }
}

.btn-fav {
  background: transparent none repeat scroll 0 0;
  border: medium none;
  font-size: 12px;
  padding: 0;
  position: absolute;
  right: 10px;
  top: -30px;
  i {
    margin-right: 6px;
    color: #cc3333;
  }
  &:hover {
    text-decoration: underline;
  }
}