.apartments {
    .col-xs-1,
    .col-sm-1,
    .col-md-1,
    .col-lg-1,
    .col-xs-2,
    .col-sm-2,
    .col-md-2,
    .col-lg-2,
    .col-xs-3,
    .col-sm-3,
    .col-md-3,
    .col-lg-3,
    .col-xs-4,
    .col-sm-4,
    .col-md-4,
    .col-lg-4,
    .col-xs-5,
    .col-sm-5,
    .col-md-5,
    .col-lg-5,
    .col-xs-6,
    .col-sm-6,
    .col-md-6,
    .col-lg-6,
    .col-xs-7,
    .col-sm-7,
    .col-md-7,
    .col-lg-7,
    .col-xs-8,
    .col-sm-8,
    .col-md-8,
    .col-lg-8,
    .col-xs-9,
    .col-sm-9,
    .col-md-9,
    .col-lg-9,
    .col-xs-10,
    .col-sm-10,
    .col-md-10,
    .col-lg-10,
    .col-xs-11,
    .col-sm-11,
    .col-md-11,
    .col-lg-11,
    .col-xs-12,
    .col-sm-12,
    .col-md-12,
    .col-lg-12 {
        padding: 0 5px;
    }
    .apartment {
        background: #DCDCDC;
        border-radius: 3px;
        box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
        position: relative;
        margin: 10px 0;
        height: 202px;
        .labels {
            left: 20px;
            position: absolute;
            top: 10px;
            .label {
                border-radius: 2px;
                color: #fff;
                font-weight: 300;
                padding-bottom: 1px;
                padding-top: 1px !important;
            }
        }
        .tc {
            background: #fff none repeat scroll 0 0;
            border-radius: 50%;
            font-size: 15px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            width: 30px;
        }
        @media(max-width: 1024) {
            height: 300px !important;
        }
        @media(max-width: 768px) {
            height: 270px !important;
        }
        @media(max-width: 360px) {
            min-height: 246px;
        }
        @media(max-width: 320px) {
            min-height: 230px;
        }
        .apartment-content {
            background: #fff;
            height: 90px;
            overflow: hidden;
            .title {
                border-right: 1px solid #eee;
                width: 50%;
                float: left;
                padding: 15px;
                padding: 8px 20px;
                background: #fff;
                height: 100%;
                h3 {
                    font-weight: 400;
                    a {
                        color: #747c8a;
                        line-height: 1.58;
                        font-size: 14px;
                    }
                }
            }
            .apartment-price {
                width: 20%;
                float: left;
                font-size: 16px;
                text-align: center;
                padding: 14px 0;
                position: relative;
                height: 100%;
                .currency {

                    font-size: 10px;
                }
                span {
                    font-size: 10px;
                    display: block;
                    text-transform: capitalize;
                }
            }
            .author {
                padding: 15px;
                background: #fafafa;
                height: 100%;
                img {
                    border-radius: 2px;
                    height: 40px;
                    margin-left: 18px;
                    margin-top: 8px;
                    width: 40px;
                }
                span {
                    font-size: 12px;
                    margin-left: 15px;
                    margin-top: 5px;
                    position: absolute;
                    @media(max-width: 420px) {
                        display: none;
                    }
                }
                .fa-check-circle {
                    color: #75c74c;
                }
                .city {
                    margin-top: 30px !important;
                }
            }
        }
        .featured-img {
            .favorited {
                color: #f9ca56;
                background: none;
            }
            i {
                color: #fff;
                position: absolute;
                right: 12px;
                top: 8px;
            }
            img {
                width: 100%;
                border-radius: 2px 2px 0 0;
            }
        }
    }
    .apartment-medium {
        .apartment-content {
            border-radius: 0 0 3px 3px;
            bottom: 0;
            height: 65px;
            position: absolute;
            width: 100%;
            .title {
                width: 70%;
                padding: 1px 20px;
                h3 {
                    margin: 10px 0;
                    a {
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1.6;
                        /* number of lines to show */
                        line-height: 1.6;
                        /* fallback */
                        max-height: 45px;
                        /* fallback */
                        font-size: 13px;
                    }
                }
            }
            .apartment-price {
                width: 30%;
            }
            .author {
                position: absolute;
                top: 25px;
                right: 25px;
                background: none;
                padding: 0;
                img {
                    box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.14);
                }
            }
        }
    }
}