
.cats-box-nav{
	max-width:936px;
	margin:0 auto;
	position: absolute;
	top: 0;
	> ul{
		@media (max-width:1200px) {
			display:none;
		}
		margin:0;
		padding:0;
		> li{
			display:block;
			float:left;
			> a{
				color: #ffffff;
				display: block;
				height: 50px;
				margin-top: 0;
				position: relative;
				width: 50px;
				i{
					position:absolute;
					top:50%;
					line-height:0;
					left:0;
					width:100%;
					text-align:center;
					font-size:30px;
				}
			}
			&.active > a, &.active > a:hover{
				background:$white !important;
				color:$priColor;
			}
		}
		.master {
			border-bottom: 1px dashed #eee;
			font-weight: 600 !important;
		}
	}

}


.mega-toggle{
	width:175px;
	height:54px;
	position:relative;
	padding-top: 12px;
	border-left: 1px dashed #eee;
	border-right: 1px dashed #eee;
	padding-left: 10px;
	a{
		color: #686868;
		text-display: none;
	}
	a:hover{
		color:red;
	}

	i {
		color: #b7b7b7;
		font-size: 14px;
		line-height: 0;
		position: absolute;
		right: 15px;
		top: 50%;
		z-index: 999;
	}
}

.mega-dropdown{
  width:230px;
  height:54px;
  border:2px solid $terColor;
  position:relative;
  &.active{
	z-index:200;
	&:before{
	  content:"";
	  position:absolute;
	  top:-2px;
	  right:-2px;
	  left:-2px;
	  height:86px;
	  background:$white;
	  z-index:18;
	  border:2px solid $secColor;

	  -webkit-box-shadow:0 0 21px 9px rgba(0, 0, 0, 0.14); -moz-box-shadow:0 0 21px 9px rgba(0, 0, 0, 0.14); box-shadow:0 0 21px 9px rgba(0, 0, 0, 0.14);

	}
	&:after{
	  content:"";
	  position:absolute;
	  left:0;
	  top:2px;
	  width:100%;
	  height:80px;
	  background:$white;
	  z-index:25;

	}
	> i{
	  color:$tc-default;
	  z-index:999;
		&:hover{
			cursor: pointer;
			color: red;
		}
	}
	.mega-content{
	  display:block;
	  & > .inner{
		padding:30px;
	  }
	}
  }
  > button{
	border:none;
	background:none;
	font-size:14px;
	line-height:0;
	top:0;
	height:100%;
	width:100%;
	position:absolute;
	left:0;
	padding:0 30px 0 15px;
	z-index:60;
	text-align:left;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  > i{
	position:absolute;
	right:15px;
	top:50%;
	font-size:14px;
	line-height:0;
	color:$frColor;
	  z-index:999;
	  &:hover{
		  cursor: pointer;
		  color: red;
	  }
  }
}
.mega-content{
  display:none;
  width:320px;
  background:#fff;
  border:2px solid $secColor;
  position:absolute;
  top:100%;
  left:-2px;
  z-index:21;
  margin-top:30px;
  -webkit-box-shadow:0 0 21px 9px rgba(0, 0, 0, 0.14); -moz-box-shadow:0 0 21px 9px rgba(0, 0, 0, 0.14); box-shadow:0 0 21px 9px rgba(0, 0, 0, 0.14);
  .mega-filtered-search &{
	border-color:$priColor;
  }
}
.dropdown-wrap{
	z-index: 9999;
  &.active{
	.mega-content{
	  display:block;
	}
  }
  .mega-content{
	margin-top:0;
	border:1px solid rgb(241, 241, 241);
	-webkit-box-shadow: 0px 5px 0px 0px rgba(0, 0, 0, 0.14);
	-moz-box-shadow: 0px 5px 0px 0px rgba(0, 0, 0, 0.14);
	box-shadow: 0px 5px 0px 0px rgba(0, 0, 0, 0.14);
	> .inner{
	  padding:30px;
	}
  }

}
.mega-list{
  & > header{
	font-size:14px;
	color:$tc-sec;
	text-transform:capitalize;
	position:relative;
	padding-left:30px;
	i{
	  color:$secColor;
	  font-size:24px;
	  position:absolute;
	  left:0;
	  top:50%;
	  line-height:0;
	  margin-top:-4px;
	}
  }
  ul{
	margin:15px -30px 0;
	padding:0 0 0 30px;
	font-size:13px;
	li{
	  display:block;
	  width:25%;
	  float:left;
	  padding-right:40px;
	  position:relative;
	  &:after{
		content: "\f105";
		font-family:"FontAwesome";
		position:absolute;
		right:33px;
		top:50%;
		line-height:0;
	  }
	  a{
		display:block;
		color:inherit;
	  }
	}
  }
}

.mega-filtered-search{
  float:left;
  height:54px;
  position:relative;
	width: 100%;


  .mega-dropdown{
	height:50px;
	position:absolute;
	left:0;
	top:0;
	border-left: 1px dashed #eee;

  }
  .search-widget{
	height:54px;
	background:none;
  }
}

.category-list{
  margin:0;
  padding:20px 0;
  position:relative;
  z-index:10;
  a{
	text-transform:capitalize;
  }
  > li{
	display:block;
	> a{
	  display:block;
	  padding:12px 50px 12px 65px;
	  font-size:14px;
	  color:inherit;
	  text-decoration:none;
	  position:relative;
	  i{
		position:absolute;
		top:50%;
		left:15px;
		width:50px;
		text-align:center;
		line-height:0;
		font-size:22px;
	  }
	  &:after{
		content: "\f105";
		font-family:"FontAwesome";
		position:absolute;
		right:30px;
		top:50%;
		line-height:0;
	  }
	}
	&:hover{
	  > a{
		background:$priColor;
		color:$white;
	  }
	  > ul{
		visibility:visible;
		filter:alpha(opacity=100);
		opacity:1;

	  }
	}
	> ul{
	  visibility:hidden;
	  filter:alpha(opacity=0);
	  opacity:0;
	  position:absolute;
	  left:100%;
	  z-index:-1;
	  width:100%;
	  top:-2px;
	  bottom:-2px;
	  overflow-y:auto;
	  background:$priColor;
	  padding:20px 0;
	  li {
		display:block;
		a{
		  color:$white;
		  display:block;
		  padding:12px 30px;
		  font-size:14px;
		  text-decoration:none;
		  &:hover{
			background:rgba(0,0,0,0.05);
		  }
		}
	  }
	}
  }
}

.dropdown-wrap {
  text-align:left;
  position:relative;
  &.active .basic-dropdown{
	display:block;
  }
  .basic-dropdown{
	display:none;
	z-index:50;
	background:$white;
	width:200px;
	position:absolute;
	right:0;
	top:100%;
	-webkit-box-shadow:0 0 9px 1px rgba(0, 0, 0, 0.14); -moz-box-shadow:0 0 9px 1px rgba(0, 0, 0, 0.14); box-shadow:0 0 9px 1px rgba(0, 0, 0, 0.14);
	&.wide{
	  width:370px;
	}
	&:before{
	  font-family:"FontAwesome";
	  content:"\f0d8";
	  color:$white;
	  font-size:22px;
	  position:absolute;
	  right:15px;
	  line-height:0;
	  top:-2px;
	}
  }
}
.country-list{
  margin:0;
  padding:0;
	ul{
		margin:0;
		padding:0;
		i{
			padding-right: 10px;
		}
		&:hover{
			background: transparent;
		}
	}
  li{
	display:block;
	a{
	  display:block;
	  padding:12px 20px;
	  text-decoration:none;
		font-size: 13px;
	  color:inherit;
	  img{
		position:relative;
		top:-1px;
		margin-right:10px;
	  }
	  &:hover{
		background:$fvColor;
	  }
	}
	  ul{
		  padding: 0;
		  margin: 0;
	  }
  }

}
.social-drop-list{
  padding:0;
  margin:0;
  li{
	display:block;
	a{
	  &:hover{
		background:$fvColor;
	  }
	  display:block;
	  padding:15px 15px 15px 50px;
	  text-decoration:none;
	  color:inherit;
	  position:relative;
	  .fa{
		width:28px;
		height:28px;
		top:12px;
		left:15px;
		line-height:28px;
		text-align:center;
		position:absolute;
		color:$white;
		border-radius:50%;
		font-size:11px;
		&.fa.fa-facebook{
		  background:$fb-bg;
		}
		&.fa.fa-twitter{
		  background:$twitter-bg;
		}
		&.fa.fa-linkedin{
		  background:$linkedin-bg;
		}
		&.fa.fa-instagram{
		  background:$insta-bg;
		}
		&.fa.fa-google-plus{
		  background:$gplus-bg;
		}
	  }
	}
  }
}
.mega-dropdown-wrap{
	hr{
		margin:10px 0;
	}
  position:relative;
  .mega-dropdown{
	&:before,&:after{
	  display:none !important;
	}
	width:100%;
	border-width:1px;
	height:42px;
	max-width:570px;
	.mega-content{
	  min-width:200px;
	  width:50%;
	  margin-top:0;
	  border:1px solid $terColor;

	  -webkit-box-shadow:none; -moz-box-shadow:none; box-shadow:none;
	  background:$fvColor;
	  left:-1px;
	  .category-list > li > ul{
		background:$white;
		border-bottom:1px solid $terColor;
		border-right:1px solid $terColor;
		top:0;
		bottom:0;
		li > a{
		  color:$tc-default;
		  &:hover{
			color:$tc-default;
			background:$fvColor;
		  }
		}
	  }
	}
  }
}