.breadcrumb {
    background-color: transparent;
    font-size: 12px;
    list-style: outside none none;
    padding: 18px 0 16px;
    margin-bottom: 0;
    ul {
        margin: 0;
        padding: 0;
        li {
            display: inline-block;
            text-transform: capitalize;
            &+li:before {
                content: "\f101";
                font-family: "FontAwesome";
                width: 24px;
                text-align: center;
                display: inline-block;
            }
            a {
                color: inherit;
            }
            span {
                @media (max-width: 992px) {
                    display: inline-block;
                    margin-top: 5px;
                }
                background:$white;
                padding:8px 30px 8px 15px;
                margin-right:4px;
                position:relative;
                a {
                    position: absolute;
                    right: 0;
                    width: 30px;
                    text-align: center;
                    height: 100%;
                    top: 0;
                    i {
                        position: absolute;
                        width: 100%;
                        top: 50%;
                        left: 0;
                        line-height: 0;
                        color: $frColor;
                        font-size: 10px;
                    }
                }
            }
        }
    }
    @media (max-width:768px) {
        display: none;
    }
}

.cd-breadcrumb {
    ol {
        li {
            a {
                text-decoration: none !important;
            }
        }
    }
}


/* --------------------------------

Basic Style

-------------------------------- */

.cd-breadcrumb,
.cd-multi-steps {
    width: 90%;
    max-width: 768px;
    padding: 0.5em 1em;
    margin: 1em auto;
    background-color: #edeff0;
    border-radius: .25em;
}

.cd-breadcrumb:after,
.cd-multi-steps:after {
    content: "";
    display: table;
    clear: both;
}

.cd-breadcrumb li,
.cd-multi-steps li {
    display: inline-block;
    float: left;
    margin: 0.5em 0;
}

.cd-breadcrumb li::after,
.cd-multi-steps li::after {
    /* this is the separator between apartments */
    display: inline-block;
    content: '\00bb';
    margin: 0 .6em;
    color: #959fa5;
}

.cd-breadcrumb li:last-of-type::after,
.cd-multi-steps li:last-of-type::after {
    /* hide separator after the last apartment */
    display: none;
}

.cd-breadcrumb li>*,
.cd-multi-steps li>* {
    /* single step */
    display: inline-block;
    font-size: 1.4rem;
    color: #2c3f4c;
}

.cd-breadcrumb li.current>*,
.cd-multi-steps li.current>* {
    /* selected step */
    color: #96c03d;
}

.cd-breadcrumb a:hover,
.no-touch .cd-multi-steps a:hover {
    /* steps already visited */
    color: #96c03d;
}

.cd-breadcrumb.custom-separator li::after,
.cd-multi-steps.custom-separator li::after {
    /* replace the default arrow separator with a custom icon */
    content: '';
    height: 16px;
    width: 16px;
    background: url(../img/cd-custom-separator.svg) no-repeat center center;
    vertical-align: middle;
}

.cd-breadcrumb.custom-icons li>*::before,
.cd-multi-steps.custom-icons li>*::before {
    /* add a custom icon before each apartment */
    content: '';
    display: inline-block;
    height: 20px;
    width: 20px;
    margin-right: .4em;
    margin-top: -2px;
    background: url(../img/cd-custom-icons-01.svg) no-repeat 0 0;
    vertical-align: middle;
}

.cd-breadcrumb.custom-icons li:not(.current):nth-of-type(2)>*::before,
.cd-multi-steps.custom-icons li:not(.current):nth-of-type(2)>*::before {
    /* change custom icon using image sprites */
    background-position: -20px 0;
}

.cd-breadcrumb.custom-icons li:not(.current):nth-of-type(3)>*::before,
.cd-multi-steps.custom-icons li:not(.current):nth-of-type(3)>*::before {
    background-position: -40px 0;
}

.cd-breadcrumb.custom-icons li:not(.current):nth-of-type(4)>*::before,
.cd-multi-steps.custom-icons li:not(.current):nth-of-type(4)>*::before {
    background-position: -60px 0;
}

.cd-breadcrumb.custom-icons li.current:first-of-type>*::before,
.cd-multi-steps.custom-icons li.current:first-of-type>*::before {
    /* change custom icon for the current apartment */
    background-position: 0 -20px;
}

.cd-breadcrumb.custom-icons li.current:nth-of-type(2)>*::before,
.cd-multi-steps.custom-icons li.current:nth-of-type(2)>*::before {
    background-position: -20px -20px;
}

.cd-breadcrumb.custom-icons li.current:nth-of-type(3)>*::before,
.cd-multi-steps.custom-icons li.current:nth-of-type(3)>*::before {
    background-position: -40px -20px;
}

.cd-breadcrumb.custom-icons li.current:nth-of-type(4)>*::before,
.cd-multi-steps.custom-icons li.current:nth-of-type(4)>*::before {
    background-position: -60px -20px;
}

@media only screen and (min-width: 768px) {
    .cd-breadcrumb,
    .cd-multi-steps {
        padding: 0 1.2em;
    }
    .cd-breadcrumb li,
    .cd-multi-steps li {
        margin: 1.2em 0;
    }
    .cd-breadcrumb li::after,
    .cd-multi-steps li::after {
        margin: 0 1em;
    }
    .cd-breadcrumb li>*,
    .cd-multi-steps li>* {
        font-size: 1.6rem;
    }
}


/* --------------------------------

Multi steps indicator

-------------------------------- */

@media only screen and (min-width: 768px) {
    .cd-multi-steps {
        /* reset style */
        background-color: transparent;
        padding: 0;
        text-align: center;
    }
    .cd-multi-steps li {
        position: relative;
        float: none;
        margin: 0.4em 40px 0.4em 0;
    }
    .cd-multi-steps li:last-of-type {
        margin-right: 0;
    }
    .cd-multi-steps li::after {
        /* this is the line connecting 2 adjacent apartments */
        position: absolute;
        content: '';
        height: 4px;
        background: #edeff0;
        /* reset style */
        margin: 0;
    }
    .cd-multi-steps li.visited::after {
        background-color: #96c03d;
    }
    .cd-multi-steps li>*,
    .cd-multi-steps li.current>* {
        position: relative;
        color: #2c3f4c;
    }
    .cd-multi-steps.custom-separator li::after {
        /* reset style */
        height: 4px;
        background: #edeff0;
    }
    .cd-multi-steps.text-center li::after {
        width: 100%;
        top: 50%;
        left: 100%;
        -webkit-transform: translateY(-50%) translateX(-1px);
        -moz-transform: translateY(-50%) translateX(-1px);
        -ms-transform: translateY(-50%) translateX(-1px);
        -o-transform: translateY(-50%) translateX(-1px);
        transform: translateY(-50%) translateX(-1px);
    }
    .cd-multi-steps.text-center li>* {
        z-index: 1;
        padding: .6em 1em;
        border-radius: .25em;
        background-color: #edeff0;
    }
    .no-touch .cd-multi-steps.text-center a:hover {
        background-color: #2c3f4c;
    }
    .cd-multi-steps.text-center li.current>*,
    .cd-multi-steps.text-center li.visited>* {
        color: #ffffff;
        background-color: #96c03d;
    }
    .cd-multi-steps.text-center.custom-icons li.visited a::before {
        /* change the custom icon for the visited apartment - check icon */
        background-position: 0 -60px;
    }
    .cd-multi-steps.text-top li,
    .cd-multi-steps.text-bottom li {
        width: 80px;
        text-align: center;
    }
    .cd-multi-steps.text-top li::after,
    .cd-multi-steps.text-bottom li::after {
        /* this is the line connecting 2 adjacent apartments */
        position: absolute;
        left: 50%;
        /* 40px is the <li> right margin value */
        width: calc(100% + 40px);
    }
    .cd-multi-steps.text-top li>*::before,
    .cd-multi-steps.text-bottom li>*::before {
        /* this is the spot indicator */
        content: '';
        position: absolute;
        z-index: 1;
        left: 50%;
        right: auto;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        transform: translateX(-50%);
        height: 12px;
        width: 12px;
        border-radius: 50%;
        background-color: #edeff0;
    }
    .cd-multi-steps.text-top li.visited>*::before,
    .cd-multi-steps.text-top li.current>*::before,
    .cd-multi-steps.text-bottom li.visited>*::before,
    .cd-multi-steps.text-bottom li.current>*::before {
        background-color: #96c03d;
    }
    .no-touch .cd-multi-steps.text-top a:hover,
    .no-touch .cd-multi-steps.text-bottom a:hover {
        color: #96c03d;
    }
    .no-touch .cd-multi-steps.text-top a:hover::before,
    .no-touch .cd-multi-steps.text-bottom a:hover::before {
        box-shadow: 0 0 0 3px rgba(150, 192, 61, 0.3);
    }
    .cd-multi-steps.text-top li::after {
        /* this is the line connecting 2 adjacent apartments */
        bottom: 4px;
    }
    .cd-multi-steps.text-top li>* {
        padding-bottom: 20px;
    }
    .cd-multi-steps.text-top li>*::before {
        /* this is the spot indicator */
        bottom: 0;
    }
    .cd-multi-steps.text-bottom li::after {
        /* this is the line connecting 2 adjacent apartments */
        top: 3px;
    }
    .cd-multi-steps.text-bottom li>* {
        padding-top: 20px;
    }
    .cd-multi-steps.text-bottom li>*::before {
        /* this is the spot indicator */
        top: 0;
    }
}