.form-wrapper {
  position: relative;
  border: 1px solid #eee;
  z-index: 200;
  .elements-block {
    min-height: 500px;
    &.style-gray > .inner {
      background: $fvColor;
    }
    & > .inner {
      padding: 90px 90px 10px;
      @media (max-width: 767px) {
        padding: 20px;
      }
      background: $white;
      label,
      .label {
        line-height: 40px;
        display: inline-block;
        font-weight: 400;
        font-size: $h5;
        color: $tc-sec;
      }
    }
  }
}

.form_footer {
  background: #fff;
  padding: 50px;
}

input[type="text"],
input[type="url"],
input[type="email"],
input[type="password"],
input[type="tel"],
textarea {
  border: 1px solid $terColor;
  outline: none;
  box-shadow: none;
  height: 42px;
  padding: 0 15px;
}

textarea {
  padding: 15px;
  height: 215px;
}

.labeled-input textarea {
  max-width: 100%;
  width: 100%;
}

label.required,
.label.required {
  &:after {
    content: "*";
    color: $c12;
    font-size: 18px;
    padding-left: 5px;
  }
}

.radio-dropdown {
  background: $white;
  &.wide {
    max-width: 100%;
  }
  max-width: 280px;
  height: 40px;
  line-height: 40px;
  border: 1px solid $terColor;
  position: relative;
  z-index: 50;
  &.active {
    z-index: 55;
    ul {
      visibility: visible;
      filter: alpha(opacity=100);
      opacity: 1;
      top: 100%;
      background: $white;
      li {
        list-style: none;
      }
    }
  }
  .fa {
    position: absolute;
    top: 50%;
    z-index: 0;
    right: 15px;
    line-height: 0;
    color: $frColor;
    font-size: 14px;
  }
  & > button {
    z-index: 20;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: none;
    border: none;
    text-align: left;
    padding: 0 30px 0 15px;
    color: $tc-default;
    text-transform: capitalize;
    font-size: 12px;
  }
  ul {
    visibility: hidden;
    filter: alpha(opacity=0);
    opacity: 0;
    border: 1px solid $terColor;
    position: absolute;
    top: 110%;
    left: -1px;
    right: -1px;
    height: 212px;
    display: block;
    padding: 0;
    margin: 0;
    overflow-y: auto;
    font-size: 12px;
    li {
      margin: 0;
      display: block;
      position: relative;
      padding-left: 10px;
      & > label {
        width: 100%;
        font-weight: normal;
        margin: 0;
        &:before {
          left: 15px;
        }
        span {
          padding-left: 10px;
        }
      }
      &:hover {
        background: $fvColor;
      }
      input:checked + label {
        background: $fvColor;
      }
    }
  }
}

.custom-radio,
.custom-checkbox {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  &:hover {
    label:before {
      content: "\f00c";
      font-family: "FontAwesome";
    }
  }
  &.square-style {
    input + label:before {
      border-radius: 0;
    }
  }
  input {
    display: none;
    & + label {
      font-size: 13px !important;
      &.big-font {
        font-size: 16px !important;
      }
      padding-left: 30px;
      position: relative;
      cursor: pointer;
      color: $tc-default !important;
      &:before {
        background: $white;
        content: "";
        border-radius: 50%;
        height: 22px;
        width: 22px;
        line-height: 22px;
        text-align: center;
        font-size: 9px;
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -11px;
        color: $priColor;
        border: 1px solid $terColor;
      }
    }
    &:checked + label {
      color: $tc-sec !important;
    }
    &:checked + label:before {
      content: "\f00c";
      font-family: "FontAwesome";
    }
  }
}

.field-block {
  margin-bottom: 20px;
}

.field-block-md {
  margin-bottom: 10px;
}

.icon-field {
  max-width: $w1;
  position: relative;
  padding-left: 48px;
  & > span {
    position: absolute;
    left: 0;
    top: 0;
    width: 49px;
    background: $fvColor;
    text-align: center;
    line-height: 40px;
    border: 1px solid $terColor;
  }
  & > input {
    width: 100% !important;
    display: block;
  }
  label.error-msg {
    color: $c12;
    position: relative;
    margin-top: 10px;
    display: block;
    margin-left: -48px;
  }
}

.attributes {
  border-bottom: 1px dashed #eee;
  border-top: 1px dashed #eee;
  padding: 50px 0 30px;
  margin-bottom: 20px;
  .icon-field {
    max-width: 100%;
    span {
      width: 90px;
      font-size: 12px;
    }
    input {
      padding-left: 55px;
    }
  }
}

.elements-block .table {
  width: 100%;
  text-align: left;
  td {
    padding: 10px 0;
  }
  thead {
    h1,
    h2,
    h3,
    h4,
    h5 {
      margin: 0;
    }
    border-bottom: 1px solid $c19;
    color: $tc-sec;
    tr:last-child td {
      padding-bottom: 20px;
    }
  }
  tbody {
    border-bottom: 1px solid $c19;
    tr:last-child td {
      padding-bottom: 20px;
    }
  }
  tfoot {
    border-bottom: 1px solid $c19;
    color: $tc-sec;
    td {
      padding: 20px 0;
    }
  }
}

.custom-check {
  position: relative;
  width: 18px;
  height: 18px;
  top: 3px;
  display: inline-block;
  input {
    display: none;
  }
  label {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
    &:before {
      font-family: "adspoticons";
      content: "\e90a";
      color: inherit;
      font-size: 16px;
      left: 2px;
      position: absolute;
      top: -2px;
    }
  }
  input:checked + label:before {
    content: "\e90b";
    color: $secColor;
  }
}

.labeled-input {
  position: relative;
  & > label {
    cursor: text;
    font-weight: normal !important;
    color: $tc-default;
    padding: 0 15px;
    line-height: 40px;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    &.error-msg {
      position: relative;
      padding: 0;
      margin-top: 10px;
      color: $c12;
    }
  }
}

.termStatement {
  @media (max-width: 767px) {
    display: block;
    margin-top: 10px;
  }
}

.valid-block {
  position: relative;
  &:after {
    font-family: "FontAwesome";
    content: "\f00c";
    position: absolute;
    left: 100%;
    margin-left: 10px;
    width: 16px;
    height: 16px;
    top: 50%;
    margin-top: -8px;
    border-radius: 50%;
    background: $secColor;
    text-align: center;
    line-height: 16px;
    font-size: 7px;
    color: #fff;
  }
}

.slim {
  width: 100px;
  height: 100px;
  background: #f8f8f8;
  border: 2px dashed #eee;
  border-radius: 3px;
  float: left;
  margin-right: 5px;
  margin-bottom: 5px;
}
.uzr-panels{
  .slim {
    width: 100%;
    height: auto;
  }
}
.slim-btn {
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
}

.slim .slim-area .slim-drip,
.slim .slim-area .slim-status,
.slim .slim-area .slim-result {
  position: absolute;
}

.input-group {
  width: 100%;
  .select2 {
    width: 100%;
  }
}

.error-msg {
  color: #F465AF;
  font-size: 11px;
}

.has-error .form-control, .has-error .select2,
.has-error .input-group-addon, .has-error .trumbowyg-box {
  border-color: #F465AF;
}

.input-group.has-error {
  .error-msg {
    color: #F465AF;
    font-size: 10px;
    position: absolute;
    left: 0;
    bottom: -26px;
  }
}

.select2 {
  width: 100%;
  max-width: 100%;
  border: 1px solid #eee;
  padding: 8px;
  border-radius: 3px;
  background: #fff;
}

.select2-city {
  .select2-container--default .select2-selection--single .select2-selection__arrow::before {
    color: #b7b7b7;
    content: "";
    display: block;
    font-family: FontAwesome;
    font-size: 14px;
    height: 20px;
    margin-left: 7px;
    padding-top: 10px;
    width: 20px;
  }
}

.top-search {
  select {
    border: 1px solid #eee !important;
  }
  select::after {
    display: block;
    content: "";
    width: 10px;
    height: 55px;
    position: absolute;
    right: 0;
  }
  .select2 {
    height: 55px;
    background: #fff;
    border: none;
    padding: 0;
    @media (max-width: 767px) {
      width: 100% !important;
    }
  }
  .select2-container--default .select2-selection--single .select2-selection__rendered {
    border: 1px solid #eee;
    line-height: 53px;
    font-size: 14px;
    padding-left: 18px;
  }
}

.select2-container--default {
  .select2-search--dropdown {
    .select2-search__field {
      border: 1px solid #eee;
    }
  }
}

.select2-dropdown {
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.14);
  border: none;
  border-radius: 0;
  .select2-search__field {
    padding: 10px;
    background: #f8f8f8;
  }
}

.select2-container--default {
  .select2-selection--single {
    background: #fff none repeat scroll 0 0;
    border: none;
    border-radius: 2px !important;
    .select2-selection__placeholder {
      color: #aeaeae;
      font-size: 14px;
      font-weight: 400;
    }
    .select2-selection__arrow b {
      display: none;
    }
    .select2-selection__arrow::before {
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      font-family: FontAwesome;
      color: #b7b7b7;
      font-size: 14px;
      padding-top: 10px;
      margin-left: -10px;
    }
    .select2-selection__arrow::before {
      margin-left: -14px;
      padding-top: 16px;
    }
    .select2-selection__clear {
      background: #fff none repeat scroll 0 0;
      color: #ddd;
      cursor: pointer;
      display: block;
      float: right;
      font-size: 25px;
      font-weight: normal;
      z-index: 999;
    }
  }
}

.error-msg {
  span {
    background: #cc3333 none repeat scroll 0 0;
    border-radius: 50%;
    box-shadow: 0 2px 10px 4px rgba(0, 0, 0, 0.14);
    color: #fff;
    font-size: 12px;
    height: 20px;
    line-height: 20px;
    position: absolute;
    right: 0;
    text-align: center;
    top: 14px;
    width: 20px;
  }
}

.select2-container--default .select2-results__group {
  border-top: 1px solid #eee;
  padding: 0px;
}

.select2-container--default .select2-results__option .select2-results__option:first-child {
  background: #6286A9;
  color: #fff;
  &:hover {
    background: #77C04B;
  }
}

.has-error i {
  bottom: 0;
  color: #eee;
  position: absolute;
  right: 25px;
  top: 13px;
}

.textarea-editor {
  margin-bottom: 15px;
}

.trumbowyg-box,
.trumbowyg-editor {
  min-height: 175px;

}
